@mixin allCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin btns {
  outline: none;
  transition: all 0.3s ease-in;
  &:hover,
  &:focus {
    color: $red;
    background-color: $navy;
  }
}
@mixin input {
  outline: none;
  border: 2px solid $navy;
  &::placeholder {
    transition: all 0.9s ease-in;
  }
  &:hover,
  &:focus,
  &:active {
    background-color: $gray;
  }
  &:hover::placeholder,
  &:active::placeholder,
  &:focus::placeholder {
    text-indent: 500px;
  }
}

@mixin bounceIn {
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  transition-duration: 0.5s;

  &:hover,
  &:focus,
  &:active {
    transform: scale(1.2);
    transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
    color: $red;
  }
}


@mixin af-bef( $height: 2px, $width: 110px) {
  content: "";
  display: block;
  height: $height;
  width: $width;
  margin: 20px auto;
}